import { Collapse, Label, FormGroup, Input } from 'reactstrap'
import Datetime from 'react-datetime'
import moment from 'moment'
import 'react-datetime/css/react-datetime.css'
console.log(Object.keys(moment()))
const Deadline = ({ value, onChange }) => (
  <FormGroup check>
    <Label check>
      <Input type='checkbox' checked={!!value} onChange={e => {
        if (e.target.checked) {
          onChange(moment().substract(2, 'day'))
        } else {
          onChange(null)
        }
      }} />{' '}
      限制表单提交日期
    </Label>
    <Collapse isOpen={!!value}>
      {value && <Datetime
        dateFormat='YYYY-MM-DD'
        timeFormat='HH:mm'
        locale='zh-cn'
        value={value}
        onChange={onChange}
        closeOnSelect
      />}
    </Collapse>
  </FormGroup>
)

export default Deadline